@charset "utf-8";

//=======================================================
// Util.varibles
//=======================================================

$channel: "mobile";




// * 경로
//-----------------
$path:      '../../resources/images/';
$font_path: '../../resources/fonts/';



$screen: (
  "pc_width":          1280px,
  "pc_max_width":      1920px,
  "pc_margin":         40px, //1280 이하
  "pc_max_margin":     50px, //1280 이상
  "mobile_width":      100%,
  "mobile_max_width":  100%,
  "mobile_margin":     20px,
  "mobile_max_margin": 0
) !default;




//=================
// * Layout
//-----------------
//screen
$screen_width:        map-get($screen, "#{$channel}_width");//gap: 40, content: 1200
$screen_max_width:    map-get($screen, "#{$channel}_max_width");// gap: 40, content: 1840
$screen_margin:       map-get($screen, "#{$channel}_margin");
$screen_max_margin:   map-get($screen, "#{$channel}_max_margin");

//layout
// $header_height:       100px;
// $breadcrumb_height:   40px;
// $footer_height:       342px;
// $footer_width:        $screen_width; 

//layout MO
$header_height:       52px;
$sidebar_top_height:  52px;
$footer_height:       58px;//메인에만 있음(추후 수정)

// content
$content_top_space:       40px;//content 상단 여백
$content_bottom_space:    64px; //content 하단 여백
// $btn_bottom_fixed_height: 58px;// 하단 고정형 버튼 높이
$content_footer_height: 90px;// 하단 고정형 버튼 높이


//section
$section_space_h:     40px;//section 상단 영백
$comp_space_h:        32px;//comp 상단 영백


//Grid
$grid_column: 12;
$grid_gutter: 2%; 
$grid_hgap: 20px;

// * Z-index
//-----------------
// $zindex_skip:         900 !default;
// $zindex_header:       500 !default;
// $zindex_breadcrumb:   409 !default;
